body {
    background-color: #f5f5f5;
}

.wrap {
    padding: 20px;
    width: 500px;
    height: 400px;
    animation: flicker 1.5s infinite alternate;
    margin: 0 auto;
    margin-top: 70px;
}

.ques {
    margin-top: 30px;
    margin-bottom: 20px;
    text-align: center;
}

.number {
    font-size: 30px;
    font-weight: 500;
    color: #08f;
}

.op {
    font-size: 30px;
    color: rgb(243, 51, 64);
    vertical-align: -5px;
}

.form {}

.input {
    display: block;
    width: 300px;
    height: 20px;
    margin: 0 auto;
    border-radius: 20px;
    padding: 20px;
    font-size: 25px;
    text-align: center;
    margin-bottom: 30px;
}

.button {
    display: block;
    padding: 3px 20px 5px 20px;
    color: #fff;
    background-color: #08f;
    margin: 0 auto;
    width: 100px;
    height: 45px;
    border-radius: 30px;
    transition: all 300ms;
    margin-bottom: 50px;
}

.button:hover {
    color: #08f;
    background-color: #fff;
}

.result {
    font-size: 30px;
    font-weight: 500;
    text-align: center;
}

@keyframes flicker {

    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
        box-shadow:
            0 0 .5rem #fff,
            inset 0 0 .5rem #fff,
            0 0 2rem #08f,
            inset 0 0 2rem #08f,
            0 0 4rem #08f,
            inset 0 0 4rem #08f;
    }
}